<template>
  <!-- 采薇资讯 -->
  <div class="message-box">
    <!-- 导航部分 -->
    <Nav></Nav>
    <!-- 内容部分 -->
    <section class="c-pagetitle is-load">
      <div class="c-pagetitle_fixed">
        <div class="c-pagetitle_img">
          <!-- <div class="c-pagetitle_logo"><img src="static/picture/hero_mask.svg" alt="" data-pagespeed-url-hash="1087874229" onload="pagespeed.CriticalImages.checkImageForCriticality(this);"></div> -->
        </div>
      </div>
      <div class="c-pagetitle_cnt">
        <h2 class="c-pagetitle_title">
          <span class="en">最新动态</span>
          <span class="ja">LATEST DEVELOPMENTS</span>
        </h2>
      </div>
    </section>
    <div class="c-pagetitle_page">
      <div class="businessIntro">
        <h3 class="businessTitle">
          前沿在线动态<br>
        </h3>
      </div>
    </div>
    <main class="l-main">
      <!-- 新闻部分 -->
      <div class="news-wrapper">
        <div>
          <ul class="news-title">
            <li :class="cur==item.id?'item':''" v-for="item in newsList" @click="show(item.id)" :key="item.id">
              {{ item.typeName }}
            </li>
          </ul>
          <div class="content">
            <ul>
              <li v-for="item in list" :key="item.id">
                <span class="title">{{ item.title }}</span>
                <span class="time">{{ $moment(item.creatTime).format('YYYY-MM-DD') }}</span>
              </li>
            </ul>
            <br/>
            <!-- 分页 -->
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentpage"
              :page-sizes="[1, 3, 5]"
              :page-size="pagesize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total"
            >
            </el-pagination>
          </div>
        </div>
      </div>
      <!-- 客服和回到顶部 -->
      <div class="top-wrapper">
        <Service></Service>
      </div>
      <!-- 联系我们 -->
      <Relation-i></Relation-i>
      <!-- 底部 -->
      <Footer></Footer>
    </main>
  </div>
</template>

<script>
import Footer from "../components/common/Footer.vue";
import Service from "../components/common/Service.vue";
import RelationI from "../components/common/RelationI.vue";
import Nav from "../components/Nav.vue";
export default {
  data() {
    return {
        // 查询条件
        // 一页显示几条
        pagesize: 1,
        // 当前页数
        currentpage:1,
    
         // 总数据条数
      total: 0,
      newsList: [],
      list: [],
      id: 1,
      cur:1
    };
  },
  components: { Nav, Footer, Service, RelationI },
  created() {
    this.getData();
    this.listData();
  },
  methods: {
    show(id) {
      this.id = id;
      this.cur = id;
      console.log(this.cur);
      this.listData();
    },
    getData() {
      this.$axios
        .post("http://47.243.109.87:8082/caiweiGw/newsType/list")
        .then((res) => {
          this.newsList = res.data.list;
          // console.log(res.data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    listData() {
      this.$axios
        .post(`http://47.243.109.87:8082/caiweiGw/news/list?type=${this.id}`)
        .then((res) => {
          // 新闻数据
          this.list = res.data.page.list;
          // 总条数
           this.total = res.data.page.totalCount;
          // console.log(res.data.page);
        })
        .catch((err) => {
          console.log(err);
        });
       
    },
    handleSizeChange(newpage) {
      this.pagesize = newpage
      this.listData()
    },
    handleCurrentChange(newcurrentpage) {
      this.currentpage = newcurrentpage
      this.listData
      // console.log(`当前页: ${val}`);
    },
  },
};
</script>

<style lang="scss" scoped>
.c-pagetitle_img {
  background: url('../assets/image/kv8.png') center/cover;
}
.message-box {
  position: relative;

  .news-wrapper {
    margin-left: 1.875rem;
    margin-right: 1.875rem;
    .news-title {
      display: flex;
      justify-content: space-between;
      width: 10.125rem;
      // background: salmon;
      position: absolute;
      margin-top: -1rem;
      line-height: 0.625rem;
      padding-bottom: 0.375rem;
      &:hover{
        cursor:pointer;
      }
      .item{
          color: #19ce89;
          border-bottom: 0.05rem solid #19ce89;
      }
      li {
        font-size: 0.45rem;
        color: #323232;
        font-weight: 400;
      }
    }
    .content {
      ul {
        padding-top:.7rem;
        li {
          display: flex;
          justify-content: space-between;
          list-style-type: disc;
          .title{
            font-size: .375rem;
            font-weight: 500;
            color: #333;
            line-height: .625rem;
          
          }
          .time{
            font-size: .375rem;
            color: #999;
            font-weight: 400;
          }
        }
      }
    }
  }
  .top-wrapper {
    position: fixed;
    right: 0.625rem;
    bottom: 0.625rem;
  }
}
</style>